*{
  margin: 0px;
  padding: 0px;
  border-radius: 0px;
  box-sizing: border-box;
  font-size: 110%;
}

#root{
  text-align: center;
}

.calculator-title{
  font-size: 30px ;
  background: rgb(29, 28, 28);
  width: 400px;
  padding: 10px;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 20px;
  border: 2px solid black;
  color: #4CAF50;
}

.mainCalc{
  box-sizing: border-box;
  margin: 0px;
  padding: 0px; 
  border-radius: 0px;
}

.screen-row input{
  width: 400px;
  background: #ddd;
  border: 0px;
  color: #222;
  padding: 10px;
  text-align: right;
}

input[type="button"]{
  width: 100px;
  background: #259e19c4;
  border: 1px solid #222;
  padding: 10px 20px;
  color: black;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  }
  
  input[type="button"]:active{
    background: #ccc;
  }
  